// AdicionarTarefas.js
import styled from 'styled-components';

const AdicionarTarefas = styled.button`
  color: white;
  background-color: #007bff;
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  text-align: center;
  justify-content:center;

  &:hover {
    background-color: #0056b3;
    
  }
`;

export default AdicionarTarefas;
