import React, { useState } from 'react';
import Title from './Title';
import { TaskItem, TaskCheckbox } from './TaskItem'; 
import AdicionarTarefas from './AdicionarTarefas'; 
import Grafico from './Grafico';

function ToDoList() {
  const [newTask, setNewTask] = useState('');
  const [tasks, setTasks] = useState([]);

  const handleAddTask = () => {
    if (newTask.trim() !== '') {
      setTasks([...tasks, { text: newTask, completed: false }]);
      setNewTask('');
    }
  };

  const toggleTaskCompletion = (index) => {
    const updatedTasks = tasks.map((task, i) => {
      if (i === index) {
        return { ...task, completed: !task.completed };
      }
      return task;
    });
    setTasks(updatedTasks);
  };

  const handleRemoveTask = (index) => {
    const updatedTasks = tasks.filter((_, i) => i !== index);
    setTasks(updatedTasks);
  };

  return (
    <>
      <Title>Lista de Tarefas</Title>
      <input
        type="text"
        value={newTask}
        onChange={(e) => setNewTask(e.target.value)}
        placeholder="Digite uma nova tarefa"
      />
      <AdicionarTarefas onClick={handleAddTask}>Adicionar</AdicionarTarefas>
      <ul>
        {tasks.map((task, index) => (
          <TaskItem key={index} completed={task.completed}>
            <TaskCheckbox
              type="checkbox"
              checked={task.completed}
              onChange={() => toggleTaskCompletion(index)}
            />
            {task.text}
            <button onClick={() => handleRemoveTask(index)}>Remover</button>
          </TaskItem>
        ))}
      </ul>
      <Grafico tasks={tasks} /> 
    </>
  );
}

export default ToDoList;
