import React from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement } from 'chart.js';
import GraficoT from './GraficoT'; 

ChartJS.register(Title, Tooltip, Legend, ArcElement);

function Grafico({ tasks }) {
  const completedTasks = tasks.filter(task => task.completed).length;
  const pendingTasks = tasks.filter(task => !task.completed && task.text.trim() === '').length;
  const inProgressTasks = tasks.filter(task => !task.completed && task.text.trim() !== '').length;

  const data = {
    labels: ['Concluídas', 'Pendentes', 'Em Andamento'],
    datasets: [
      {
        data: [completedTasks, pendingTasks, inProgressTasks],
        backgroundColor: ['green', 'red', 'blue'],
      },
    ],
  };

  return (
    <div>
      <h3>Proporção das Tarefas</h3>
      <GraficoT>
        <Pie data={data} />
      </GraficoT>
    </div>
  );
}

export default Grafico;
